import clsx from 'clsx'
import {
	FC,
	useCallback,
	useLayoutEffect,
	useMemo,
	useRef,
	useState,
} from 'react'
import { cssVars, appendInvisible } from '../ui/browser'
import { Point } from '../types/types'
import { getScreenDimensions, getScreenWidth, isMobile } from '../ui/browser'

const MAX_SCALE = 1

const maximizationLevels = [
	'minimized',
	'maximized',
	'super-maximized',
] as const

export const TextArt: FC<{
	content: string
}> = ({ content }) => {
	const [scale, setScale] = useState(1)
	const [drag, setDrag] = useState(false)
	const clickedPoint = useRef<Point>([0, 0])

	const [maximizationLevelIdx, setMaximizationLevelIdx] = useState<number>(0)
	const maximization = maximizationLevels[maximizationLevelIdx]
	const setMaximization = (val: typeof maximizationLevels[number]) =>
		setMaximizationLevelIdx(maximizationLevels.indexOf(val))
	const incrementMaximization = () =>
		setMaximizationLevelIdx((x) => (x + 1) % maximizationLevels.length)

	const [dummyEl, setDummyEl] = useState<HTMLPreElement | null>(null)

	const ref = useRef<HTMLPreElement>(null)

	useLayoutEffect(() => {
		const el = appendInvisible('pre')

		setDummyEl(el)

		return () => el.remove()
	}, [])

	const [naturalWidth, naturalHeight] = useMemo(() => {
		if (!dummyEl) return [0, 0]

		dummyEl.textContent = content

		const { width, height } = dummyEl.getBoundingClientRect()

		dummyEl.textContent = ''

		return [width, height]
	}, [dummyEl, content])

	const updateScale = useCallback(() => {
		if (maximization === 'super-maximized') {
			setScale(1)

			return
		}

		const { margin, sidebar, sidebarMarginRight } = cssVars

		const screenWidth = getScreenWidth()
		const negativeSpace =
			maximization === 'minimized'
				? margin * 2 + sidebar + sidebarMarginRight
				: 0

		const rawScale = (screenWidth - negativeSpace) / naturalWidth

		setScale(Math.min(rawScale, MAX_SCALE))
	}, [naturalWidth, maximization])

	useLayoutEffect(() => {
		updateScale()

		window.addEventListener('resize', updateScale)

		return () => window.removeEventListener('resize', updateScale)
	}, [updateScale])

	useLayoutEffect(() => {
		const undrag = () => setDrag(false)
		const drag = () => setDrag(true)
		const minimize = (e: KeyboardEvent) => {
			if (e.key === 'Escape') setMaximization('minimized')
		}

		document.addEventListener('mousedown', undrag)
		document.addEventListener('mousemove', drag)

		window.addEventListener('keydown', minimize)

		return () => {
			document.removeEventListener('mousedown', undrag)
			document.removeEventListener('mousemove', drag)

			window.removeEventListener('keydown', minimize)
		}
	}, [])

	useLayoutEffect(() => {
		if (maximization !== 'minimized') {
			document.body.style.userSelect = 'none'
		}

		return () => {
			document.body.style.userSelect = ''
		}
	}, [maximization])

	useLayoutEffect(() => {
		if (maximization === 'super-maximized') {
			const [x, y] = clickedPoint.current

			setTimeout(() => {
				const [width, height] = getScreenDimensions()

				window.scroll(
					x * naturalWidth - width / 2,
					y * naturalHeight - height / 2,
				)
			}, 0)
		}
	}, [maximization, naturalHeight, naturalWidth])

	return (
		<div
			className={clsx({
				'center-content': maximization === 'minimized',
				interactive: !isMobile && maximization === 'minimized',
				maximized: maximization !== 'minimized',
				'super-maximized': maximization === 'super-maximized',
			})}
		>
			{maximization !== 'minimized' && (
				<button
					type='button'
					onClick={() => setMaximization('minimized')}
					className='close-btn'
					aria-label='Close'
				>
					<span aria-hidden>×</span>
				</button>
			)}
			<pre
				ref={ref}
				onClick={(e) => {
					// useless on mobile as pinch-to-zoom is available
					if (!drag && !isMobile) {
						const { clientX, clientY } = e

						const { x, y, width, height } =
							e.currentTarget.getBoundingClientRect()

						clickedPoint.current = [
							(clientX - x) / width,
							(clientY - y) / height,
						]

						incrementMaximization()
					}
				}}
				className={'text-art'}
				translate='no'
				style={{
					transform: `scale(${scale})`,
					// https://medium.com/@sai_prasanna/simulating-css-zoom-with-css3-transform-scale-461d1b9762d6
					margin:
						maximization === 'super-maximized'
							? 0
							: `${
									-(naturalHeight - scale * naturalHeight) / 2
							  }px ${
									-(naturalWidth - scale * naturalWidth) / 2
							  }px `,
				}}
			>
				{content}
			</pre>
		</div>
	)
}
